@import url('https://fonts.googleapis.com/css2?family=Rubik:wght@300;400;500;600;700;800&family=Ubuntu:wght@400;500;700&display=swap');

* { 
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  min-height: 100vh;
  font-family: 'Rubik', 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(180deg, rgba(55, 71, 88, 0.4) 0%, rgba(55, 71, 88, 0.68) 0.01%, rgba(45, 55, 72, 0.91) 48.96%, rgba(13, 16, 22, 0.93) 87.54%) no-repeat;
}

.main-nav{
  display: flex;
  justify-content: space-between;
  padding: 2rem;
}

.main-content{
  display: flex;
}

.main-text{
  flex: 1;
}

.main-image{
  flex: 2;
  background-color: red;
}

.club{
  margin-top: 2rem;
}

.navbar{
  display: flex;
  justify-content: space-evenly;
}

.navbar-link{
  color: #fff;
}